<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('BANK_NAME') }}</label>
          <vs-input class="w-full" v-model="bank.bankName"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('COUNTRY') }}</label>
          <country-lookup :title="$t('COUNTRY')" :recordValue="bank.countryGeoId" :recordDisplay="bank.countryName" valueField="geoId" displayField="geoName" @select-record="selectCountryRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('INSTITUTION_CODE') }}</label>
          <vs-input class="w-full" v-model="bank.institutionCode"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('NATIONAL_CODE') }}</label>
          <vs-input class="w-full" v-model="bank.nationalCode"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <vs-checkbox v-model="bank.disabled">{{ $t('DISABLED') }}</vs-checkbox>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <div class="dropdown-button-container">
              <vs-button class="btnx" type="filled" @click="modifyBank('browse')">{{ $t('SUBMIT') }}</vs-button>
              <vs-dropdown vs-trigger-click>
                <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
                <vs-dropdown-menu>
                  <vs-dropdown-item @click="modifyBank('stay')">
                    <span>{{ $t('SUBMIT_STAY') }}</span>
                  </vs-dropdown-item>
                  <vs-dropdown-item @click="modifyBank('view')">
                    <span>{{ $t('SUBMIT_VIEW') }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import BankService from '@/services/bank/BankService'
import CountryLookup from '@/views/nucleus/country/CountryLookup'

export default {
  name: 'bank-modify',
  mixins: [AlertMixin],
  components: {
    CountryLookup
  },
  data: function () {
    return {
      bank: {}
    }
  },
  methods: {
    async load () {
      let self = this
      BankService.banks({
        bankId: this.$route.params.id
      }).then(function (response) {
        self.bank = response.data.bankList[0] || {}
        self.bank.disabled = self.bank.disabled === 'Y'
      })
    },
    selectCountryRecord (record) {
      this.bank.countryGeoId = record.geoId || ''
    },
    modifyBank (nextAction) {
      let self = this
      BankService.modifyBank({
        bankId: this.$route.params.id,
        bankName: this.bank.bankName,
        countryGeoId: this.bank.countryGeoId,
        institutionCode: this.bank.institutionCode,
        nationalCode: this.bank.nationalCode,
        externalTypeId: this.bank.externalTypeId,
        disabled: this.bank.disabled ? 'Y' : 'N'
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewBank', params: { id: response.data.bankId } })
          } else {
            self.$router.push({ name: 'BrowseBanks' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
